<template>
  <div class="good_detail">
   <div class="header">
    <van-nav-bar title=""  left-arrow @click-left="routergo" />
    <span v-show="collectIcon">
      <img class="collectIcon" @click="changeCollectIcon" :src="collectStas == '2'?collect1:collect2" ref="collectIcon" alt="">
    </span>
   </div>
   <div class="good_detail_content">
      <div class="carouser">
        <van-swipe :autoplay="3000" :show-indicators='indicators'>
          <van-swipe-item v-for="(item,index) in lunboList" :key="index"><img :src="item"/></van-swipe-item>
        </van-swipe>
      </div>
      <div class="detail_main">
        <div class="good_name">{{goodsName}}</div>
        <div class="good_info"><span class="pointNum">{{goodsPoint}}</span><span class="pointText"> </span>{{goodsStock}}</div>
        <span class="soldNum"><span class="redNum">{{goodsSold}}</span> </span>
      </div>
      <div class="safeguardBox">
        <ul class="safeguard clear">
          <li class="deliverIcon"><img :src="safeguard1" alt="" /><span></span></li>
          <li class="compensateIcon"><img :src="safeguard2" alt="" /><span></span></li>
          <li class="qualityIcon"><img :src="safeguard3" alt="" /><span></span></li>
        </ul>
      </div>
      <div class="purchaseBox">
        <h4></h4>
        <div class="purchaseBoxDetail" v-html="dataListInfo">
        </div>
        <!-- <h4 class="redNum"></h4>
        <p>{{useFlow}}</p>
        <h4 class="redNum"></h4>
        <p>{{statement}}</p>
        <h4 class="redNum"></h4>
        <span class="validTimes">{{validity}}</span> -->
      </div>
      <!--  -->
      <div class="footerPayBox">
        <div class="payInfoBox" v-show="bottomStatus">
          <span class="payInfoText">: <span class="redNum">{{userScore}}</span></span>
          <router-link v-if="goodsStock" :to="{path:'/order/confirmOrder',query:{id:productId}}"><span class="payBtn" :class="{payBtnBg: goodsStock}"></span></router-link>
          <span v-else class="payBtn noStockBtnBg"></span>
        </div>
      </div>
   </div>
  </div>
</template>

<script>
import { Toast } from 'vant';
import './index.less'
import safeguard1 from '@/assets/goodsDetial/icon_mall_ship.png'
import safeguard2 from '@/assets/goodsDetial/icon_mall_compensation.png'
import safeguard3 from '@/assets/goodsDetial/icon_mall_genuine.png'
import collect1 from '@/assets/goodsDetial/goods_not_collection.png'//
import collect2 from '@/assets/goodsDetial/goods_collected.png'//
export default {
  name: 'hart',
  data () {
    return {
      productId: '',//id
      lunboList: '',// 
      indicators: Boolean(false),// ()
      safeguard1: safeguard1,// 
      safeguard2: safeguard2,// 
      safeguard3: safeguard3,// 
      collectIcon: false,// 
      collectStas: '',// 
      collect1: collect1,// 1
      collect2: collect2, // 2
      goodsName: '',// 
      goodsPoint: '',//
      goodsSold: '',//
      goodsStock: '',//
      bottomStatus: false,///
      userScore: '',//
      validity: '',//
      announcements: '',//
      useFlow: '',//
      statement: '',//
      dataListInfo:''
    }
  },
  created () {
    let thisId = String(this.$route.query.id) //id
    this.productId = thisId // id
    this.$store.dispatch('commonService',{"apid": 110,
        params: {// 
        productId: thisId
      }})
    .then((result) => {
      this.bottomStatus = true;///
      this.collectIcon = true;// 
      this.collectStas = result.apidata.data.favorite_status// 
      this.lunboList = result.apidata.data.product_bananer// 
      this.goodsName = result.apidata.data.full_name// 
      this.goodsPoint = result.apidata.data.exchange_points// 
      this.goodsSold = result.apidata.data.sales_base// 
      this.goodsStock = result.apidata.data.stock_num// 
      this.userScore = result.apidata.data.userScore// 
      let proExtends = result.apidata.data.pro_extends //
      this.dataListInfo = result.apidata.data.product_describe
      // for(var i=0;i<proExtends.length;i++){
      //   if(proExtends[i].attrDicName == 'usefulTime'){//  
      //     this.validity = proExtends[i].detailDesc// 
      //   }else if(proExtends[i].attrDicName == 'announcements'){// 
      //     this.announcements = proExtends[i].detailDesc// 
      //   }else if(proExtends[i].attrDicName == 'useFlow'){// 
      //     this.useFlow = proExtends[i].detailDesc// 
      //   }else if(proExtends[i].attrDicName == 'statement'){// 
      //     this.statement = proExtends[i].detailDesc// 
      //   }
      // }
    })
    .catch((error) => {
    	console.log(error)
    });
  },
  methods: {
    routergo: function () {
      this.$router.back(-1)
    },
    changeCollectIcon: function () {// 
      let thisId = this.$route.query.id.toString() //id
      if(this.collectStas == '2'){// 1:  2
        this.$store.dispatch('commonService',{"apid": 175,//
          params: {// 
          id: thisId
        }})
        .then((result) => {
          this.collectStas = '1'
          Toast.success('')
        })
        .catch((error) => {
          console.log(error)
        });
      }else{
        this.$store.dispatch('commonService',{"apid": 177,//
          params: {// 
          ids: thisId,
          type: '2'
        }})
        .then((result) => {
          this.collectStas = '2'
          Toast.success('')
        })
        .catch((error) => {
          console.log(error)
        });
      }
    }
  },
  mounted() {
    // window.location.reload()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='less'>

</style>



