<template>
  <div class="card_content">
   <div class="header">
    <van-nav-bar title=""  left-arrow @click-left="routergo" :right-text="this.flag ? '' : '' " @click-right="onClickRight" />
   </div>
   <div class="hart_list" :class="{hart_list_bottom:!flag}">
     <van-list
       v-model="loading"
       :finished="finished"
       @load="onLoad"
       :offset="offset"
     >
     <div class="order_empty" v-show="!hasShop" ref="card">
      <div class="empty_img">
        <img src="./../../../assets/mine/noCard.png">
      </div>
      <p>~</p>
      <router-link class='go_index' to="/index"></router-link>
    </div>
     <van-checkbox-group v-model="result" @change="change">
     <div class="hart_detail" v-for="(item, index) in listData" :key="index" :name="item" v-show="hasShop">
      <div class="hart_left" v-if="item.deliver_way == 1 ? ( item.status < -1 ? ( item.product_exchange_type < 4 ? flag : false):false ) : false">
        <van-checkbox  :key="item.id"  :name="item.id"></van-checkbox>
      </div>
      <div class="card" :class="{cardActive:item.deliver_way == 1 ? ( item.status < -1 ? ( item.product_exchange_type < 4 ? flag : false):false ) : false}" >
        <div class="card_top">
          <div class="card_info" v-show="item.status == -3 ?  false: (item.status == 0 ? false:true ) ">{{ item.status == -2 ? "":(item.status == 0 ? '':'' ) }}</div>
          <div class="card_main" @click="goCardDetail({id:item.id})">
            <div class="card_price" :class="{card_price_active: item.status == 0 ? true:false}"><span>{{item.price}}</span></div>
            <div class="card_name">
              <p class="title">{{item.product_name}}</p>
              <p class="title1" v-show="item.product_exchange_type == 1 ? true : false">{{item.exchange_code}}</p>
              <p class="title2">{{item.buy_time}}</p>
            </div>
            <div class="card_img" :class="{card_img_active: item.status == 0 ? true:false}" v-show="item.status == -3 ?  false: true ">
              <img :src="item.status == 0 ? successIcon:defeatedIcon ">
            </div>
          </div>
        </div>
        <div class="circle circleLeft"></div>
        <div class="circle circleRight"></div>
        <div class="card_button" v-show="item.product_exchange_type == 1 ? ( item.status < -1 ? (item.status == -4 ? false: true) :false ) : false" v-clipboard:copy="item.exchange_code" v-clipboard:success="onCopy" v-clipboard:error="onError"></div>
        <div class="card_button card_button_no" v-show="item.product_exchange_type == 1 ? ( item.status < -1 ? (item.status == -4 ? true : false) :true ) : false" @click='copyNo'></div>
        <div class="card_button" :class="{card_button_no:item.status < -1 ? false:true}" v-show="item.product_exchange_type == 1 ? false : true" @click="goToConvert({status:item.status,orderId:item.order_id,cardId:item.id,type:item.product_exchange_type,url:item.exchange_url,code:item.exchange_code,hasExchangeCode:item.hasExchangeCode})"></div>
      </div>
     </div>
     <div class="no_data_info clear" v-show="dataFlag">...</div>
     </van-checkbox-group>
     </van-list>
     <div class="hart_bottom" v-show="flag">
      <div class="allSelect">
        <div class="aaa">
          <i class="van-icon van-icon-success van-checkbox__icon van-checkbox--round" :class="{allChecked:allSelectFlag}"  @click="allSelect"></i>
          <span class="van-checkbox__label"></span>
        </div>
      </div>
      <div class="cancel" @click='readyExchange'></div>
     </div>
   </div>
  </div>
</template>

<script>
import './index.less'
import { Toast, Dialog } from 'vant'
import {formatDate} from './date.js'
import successIcon from '@/assets/mine/my_card_success.png'
import defeatedIcon from '@/assets/mine/my_card_defeated.png'
import costpayDefeated from '@/assets/mine/my_card_costpay_defeated.png'
import costpaySuccess from '@/assets/mine/my_card_costpay_success.png'
export default {
  name: 'cardBag',
  data () {
    return {
      loading: false, // loading
      finished: false, // 
      flag: false, // 
      list: [], // 
      result: [], // 
      allSelectFlag: false, // 
      offset: Number(50), // 
      hasShop: true, // 
      successIcon: successIcon,  // 
      defeatedIcon: defeatedIcon, // 
      costpayDefeated: costpayDefeated,
      costpaySuccess: costpaySuccess,
      listData:[], // 
      pageNo:'1',
      dataFlag:false // 
    }
  },
  methods: {
    routergo: function () {
      this.$router.back(-1)
    },
    onClickRight () {
      if(this.hasShop){
        this.flag = !this.flag
        this.result = []
      }
    },
    onLoad () {
      let that = this
      this.$store.dispatch('commonService',{"apid": 115,
      params: {
        pageSize: "10",
         pageNo: String(that.pageNo),
    }})
      .then((result) => {
        if( result.apidata.code == 1) {
          that.pageNo++
          if( result.apidata.data.datas.length > 0 ) {
            let dataList = result.apidata.data.datas
            this.loading = false
            that.listData = that.listData.concat( result.apidata.data.datas )
            if(that.listData.length < 6 ) {
              that.finished = true
            }
            that.list = []   // list
            for ( let j=0;j<that.listData.length; j++){
              if( that.listData[j].deliver_way == 1 && that.listData[j].status < -1 &&  that.listData[j].product_exchange_type < 4  ) that.list.push(that.listData[j].id)
            }
          }else{
             that.finished = true
             that.loading = false
             if (that.listData.length < 1 && result.apidata.data.datas.length <1 ) {
               that.hasShop = false;
             }else{
                that.dataFlag = true;
             }
             // Toast('...');
          }
        }else{
           Toast(result.apidata.message);
        }
      })
      .catch((error) => {
        console.log(error)
      })
    },
    change () { // 
      // console.log(this.result.length,this.list.length)
      if (this.result.length == this.list.length && this.result.length >0 ) {
        this.allSelectFlag = true
      } else {
        this.allSelectFlag = false
      }
    },
    allSelect () {  // 
      this.allSelectFlag = !this.allSelectFlag
      if (this.allSelectFlag) {
        this.result = this.list
      } else {
        this.result = []
      }
    },
    copyNo(){
      Toast("")
    },
    onCopy(){
      Toast("")
    },
    onError() {
      console.log("error")
    },
    goToConvert (data) {
      // console.log(data)
      if(data.status == -2 || data.status ==-3 ){
        // alert('')
        switch (data.type) {
          case 2 :
            this.$router.push({path:'/cpsDetail',query:{entranceUrl:data.url}})   // 
            break;
          case 3:
            if( data.hasExchangeCode == 1 ) {
              this.$router.push({path:'/cpsDetail',query:{entranceUrl:data.url+data.code}})  // 
            }else{
              this.$router.push({path:'/cpsDetail',query:{entranceUrl:data.url}})  // 
            }
            break;
          case 4:
            this.$router.push({path:'/convertResults',query:{status:data.status,orderId:data.orderId,cardId:data.cardId}})  // A
            break;
          case 5:
            this.$router.push({path:'/goodsConvert',query:{orderId:data.orderId,cardId:data.cardId}}) // B
            break;
        }

      } else{
        if(data.status == 0 ){
          Toast("")
        }else{
          Toast("")
        }

      }
    },
    goCardDetail (data) {
       if( this.flag ){
        return;
      }
      this.$router.push({path: '/order/cardDetail', query: {cardId: data.id}})
    },
    readyExchange() {  // 
      let that = this
      if(  that.result.length <= 0 ) {
        Toast("")
        return false;
      }
      Dialog.confirm({
        message: ''
      }).then(() => {
        let dataString =(that.result).join()
      // console.log(dataString)
      this.$store.dispatch('commonService',{"apid": '116',
        params: {
          cardIds: dataString
        }})
        .then((result) => {
          if( result.apidata.code == 1) {
            Toast("")
            // console.log("sucess")
            that.listData = []
            that.list = []
            that.result = []
            that.pageNo = '1'
            that.onLoad()
            that.allSelectFlag = false
          }else{
            Toast(result.apidata.message)
          }
        })
        .catch((error) => {
          console.log(error)
        })
      }).catch(() => {
         // on cancel
    });
    }
  },
  mounted () {
    // alert(window.innerWidth)
  },
  filters: {
    formatDate(time) {
        var date = new Date(time);
         return formatDate(date, 'yyyy-MM-dd hh:mm:ss');
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='less'>

</style>



