<template>
  <!--  -->
  <div class="order_detial">
    <div class="orderDetialContBox">
      <!--  -->
      <div class="goodsInfoBox margin8 padding20">
        <div class="orderGoodsInfoBox clear">
          <div class="orderGoodsInfo fl">
            <img class="goodsImg" :src="goodsImg" alt="">
          </div>
          <div class="orderGoodsInfoText fl">
            <span class="goodsTitle">{{goodsTitle}}</span>
            <span class="goodsPoint"><i>{{goodsPoint}}</i></span>
            <!-- <span class="goodsValidity">{{usefulTime}}</span> -->
          </div>
        </div>
        <div class="orderPaymentBox"><i>0+{{payAmount}}</i></div>
      </div>
      <!--  -->
      <!-- <div class="instructions margin8 padding20">
        <span class="instructionsTitle"></span><br/>
        <p class="convertInfoText">{{instructions}} </p>
      </div> -->
      <!--  -->
      <div class="orderRelated padding20">
        <span class="orderNum">{{orderNum}}</span>
        <span class="orderPayment">{{payment}}</span>
        <span class="orderPaymentTime">{{payTime}}</span>
      </div>
    </div>
  </div>
</template>
<script>
import './cardDetial.less'
import { Toast } from 'vant'
import goodsImg from '@/assets/orderDetial/orderDetialGoodsImg.png'
import {formatDate} from './date.js' //
export default {
  name: 'cardDetail',
  data () {
    return {
      goodsImg: '',//
      goodsTitle: '',//
      goodsPoint: '',//
      usefulTime: '',//
      instructions: '',//
      payAmount: '',//
      orderNum: '',//
      payment: '',//
      payTime: '',//
    }
  },
  created () {
    let cardId = String(this.$route.query.cardId) //id
    this.$store.dispatch('commonService',{"apid": 117,//
        params: {// 
          cardId: cardId
      }})
    .then((result) => {
      // console.log(result)
      if(  result.apidata.code != 1){
          Toast(result.apidata.message)
          return;
        }
      this.goodsImg = result.apidata.data.image_url// LOGO
      this.goodsTitle = result.apidata.data.productDetail.fullName// 
      this.goodsPoint = result.apidata.data.productDetail.productRule.exchangePoints//
      this.usefulTime = result.apidata.data.usefulTime // 
      this.payAmount = result.apidata.data.payment_amount// 
      this.orderNum = result.apidata.data.order.orderno // 
      this.payTime = result.apidata.data.order.paymentTime// 
      this.payment = result.apidata.data.paymentType// 
      let refundInfo = result.apidata.data.productDetail.productExtendedDetailList//
      for(let i=0;i<refundInfo.length;i++){
        if(refundInfo[i].attrDicName == 'purchaseSucceedsMsg'){//
          this.instructions = refundInfo[i].detailDesc
        }
      }
    })
    .catch((error) => {
    	console.log(error)
    });
  },
  filters: {
    formatDate(time) {
      var date = new Date(time);
      return formatDate(date, 'yyyy-MM-dd hh:mm:ss');
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



