<template>
  <div class="classify clear">
  <div ref="wrapper" class='left_nav_main'>
    <ul class="left_nav">
      <li v-for="(item, index) in list" :key='index' :class='{active: firActive===item.sortName}' @click='toggle(item.sortName,item.id)'><span>{{item.sortName}}</span></li>
    </ul>
  </div>
  <div class="classify_main">
    <div class="second_nav" v-show="navFlag" ref="secondNav">
      <van-tabs v-model="active1" v-if="secList.length > 0" sticky @click="secondClick">
        <van-tab v-for="index in secList" :key="index.id" :title="index.sortName" ></van-tab>
     </van-tabs>
    </div>
    <ul class="clear list_main" :class="{marginTop : navFlag }">
      <!-- <div class="loading" v-if="loading">
        <van-loading type="spinner" color="black" />
      </div> -->
      <van-list
        :finished="finished"
        v-model="pageLoading"
        @load="onLoad"
        :offset="offset"
      >
        <!-- <van-pull-refresh v-model="loading" @refresh="onRefresh"> -->
          <li v-for="(item, index) in shopList" :key='index' @click="showGoodsDetail($event,item.id,item.existCpsEntrance,index)">
            <van-card :title="item.full_name">
            <div slot="thumb" class="classify_thumb">
              <img :src="item.image_url">
              <div class="icon_info icon_info_left" v-show="item.product_ad_attr == 1 ? ( item.product_num_status ? ( item.specialOfferFlag ? true:false ) :  true) : false">{{item.product_num_status ? ( item.specialOfferFlag ? "" : ''): ''}}</div>
              <div class="icon_info icon_info_right" v-show="item.product_ad_attr == 1 ? ( item.product_num_status ? ( item.tag ? true:false ) :  false) : ( item.tag ? true:false )">{{item.tag}}</div>
            </div>
              <div slot="desc" class="desc">
                <div><span>{{item.exchange_points}}</span> </div>
                <div>: {{item.retail_price}}</div>
              </div>
              <div slot="footer" class="foot_sale">
                : <span>{{item.sales_base}}</span>
                <button v-if="item.existCpsEntrance" class="cpsEntrance">{{item.cpsEntrance.entranceButtonName}}</button>
              </div>
            </van-card>
          </li>
          <div class="loadFinish" v-show="noDataInfo"></div>
        <!-- </van-pull-refresh> -->
      </van-list>
    </ul>
  </div>
  </div>
</template>

<script>
import './index.less'
import { Toast } from 'vant'
import BScroll from 'better-scroll'
import banner1 from '@/assets/index/banner1.png'
export default {
  name: 'classify',
  data () {
    return {
      list: [],
      active: 0,
      active1: 0,
      loading: false,
      pageLoading:false,
      finished: false,
      noDataInfo: false,
      shopList: [],
      navFlag: false,
      showLoad: false,
      banner1: banner1,
      offset: Number(10),
      allCateList:[],
      secList:[],
      firActive:'',	//id
      pageSize:10,	//
      pageNo:0,	//
      sortId:'',	//id
      sortType:0, //
      scroll:''
    }
  },
  created () {
  	// this.showLoadding()
    const that = this;
    this.$store.dispatch('commonService',{"apid": 194})
    .then((result) => {
      // console.log(result)
			// this.getGoodsList(0)	//
            if(result.apidata.code !== 1){
              Toast(result.apidata.message);
              return
            }
        	that.allCateList = result.apidata.data;
        	that.list.push({sortName:'',sortType:0})  //
        	result.apidata.data.map((item,index) => {
        		that.list.push(item)
        	})
    })
    .catch((error) => {
    	console.log(error)
    })
  },
  methods: {
    onRefresh() { //
      this.pageNo = 0;
      this.getGoodsList()
    },
    clearData() {

    },
  	getGoodsList () {
      const that = this;
      that.pageNo = that.pageNo + 1;
      if(that.pageNo > 2) that.pageLoading = true;
      this.$store.dispatch('commonService',{"apid": 195,
       params: {
        pageSize: that.pageSize,
        pageNo: that.pageNo,
        location:that.sortType,
        sortId:that.sortId
      }})
      .then((result) => {
  			if(result.apidata.code !== 1){
  				Toast(result.apidata.message);
  				return
        }
        if(that.pageNo == 1 ){
          // console.log("1111")
          that.shopList = [];
        }
          // that.pageNo = that.pageNo + 1;
          that.showLoad = false
          that.loading = false
          that.finished = false
          that.noDataInfo = false
          that.pageLoading = false;
          if (result.apidata.data.datas.length <= 0 && that.pageNo > 2) {
            that.finished = true
            that.noDataInfo = true
          }
          if (result.apidata.data.datas.length <= 5) {
            that.finished = true
          }
          // that.shopList.push(result.apidata.data.datas);
          // console.log(that.pageNo,result.apidata.data.datas)
          that.shopList = that.shopList.concat(result.apidata.data.datas);	
          if ( that.shopList.length < 5) {
            that.noDataInfo = false
          }
          if ( that.shopList.length > 5 && result.apidata.data.datas.length <= 10) {
            that.noDataInfo = true
          }

  		})
  	},
    toggle (name,id) {	//
      this.showLoadding()	//
      this.pageNo = 0;
      this.shopList = [];
      this.pageLoading = false
      this.finished = false;
      this.noDataInfo = false
    	if(name !== ''){
    		this.secList = [];
    		this.allCateList.map((item,index) => {
	      	if(item.id === id && item.sortChildren && item.sortChildren.length > 0){  //
            this.secList.push({sortType:1,sortName:'',id:item.id})  //
            this.secList = this.secList.concat(item.sortChildren) //
	      	}
	      })
    		this.active1 = 0; //tab
    		this.sortId = id	//id
        this.sortType = 1;
    		this.getGoodsList()	//
    		this.secList.length > 0 ? this.navFlag = true : this.navFlag = false;	//
    	}else {
    		this.navFlag = false
        this.sortId = '';	//id
        this.sortType = 0;
    		this.getGoodsList()
    	}
      this.firActive = name;
      
    },
    showLoadding() {
      this.loading = true;
      this.handleScroll()
      this.shopList = []
    },
    showGoodsDetail(event,goodsId,existCpsEntrance,index) { //
      const entranceUrl = this.shopList[index].cpsEntrance ? this.shopList[index].cpsEntrance.entranceUrl : ''
      if(existCpsEntrance){ //cps 
        event.target.className == "cpsEntrance" ? this.$router.push({path: '/cpsDetail', query: {entranceUrl: entranceUrl}}) : ''
      }else{  //cps
        this.$router.push({path: '/goodDetail', query: {id: goodsId}})
      }
    },
    onLoad () {
      this.getGoodsList()	    
    },
    secondClick (index) {
    	this.active1 = index;
    	this.sortId = this.secList[index].id	//id
      this.showLoadding()	//
      this.sortType = this.secList[index].sortType;
      this.pageNo = 0;
      this.shopList = [];
      this.pageLoading = false
			this.getGoodsList()	//
    },
    handleScroll () {
      // window.pageYOffset = 0
      // document.documentElement.scrollTop = 0
      // document.body.scrollTop = 0
    },
    toggleNav () {
      this.showLoad = true
      this.handleScroll()
      this.shopList = []
      this.finished = true
      this.onLoad()
    },
    _initScroll(){
      if(!this.$refs.wrapper){
        return 
      }

      this.scroll=new BScroll(this.$refs.wrapper,{
        scrollY: true,
        click:true
      })
    }
  },
  mounted() {
    const _this = this; 
    setTimeout(()=>{
      _this.scroll = new BScroll(_this.$refs.wrapper,{
        scrollY:true,
        click:true
      });
    },20)
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



